.header {
    padding: 0 0 0 20px;
    position: fixed;
    left: 50px;
    right: 50px;
    top: 40px;
    height: 80px;
    border-radius: 80px;
    background-color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 0 30px rgb(0 0 0 / 15%);
}
.logo{
  display: inline-flex;
  align-items: center;
  
}
.logo img{
width: 90px;
margin-bottom: 20PX;
}
.heroImage {
    position: fixed;
    width: 47%;
    height: 100vh;
    top: 0;
}
.overlay {
    position: absolute;
    bottom: 100%;
    left: 0;
    right: 0;
    background-color: rgb(47, 168, 177);
    opacity: 0.8;
    overflow: hidden;
    width: 100%;
    height: 0;
    transition: .5s ease;
  }
  
  .portfolio:hover .overlay {
    bottom: 0;
    height: 100%;
  } 
  .text {
    white-space: nowrap; 
    color: white;
    font-size: 40px;
    font-weight: 700;
    position: absolute;
    overflow: hidden;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
  }
